import './About.css' 

function About() {

    return (
        <div id='about' className ='about-container'>
            <h1 className='title'>About</h1>
            <div className='all-text'>
                <div className='text'>
                    <h4 className='icon'>High School <br/> <span>2018-2020</span></h4>
                    <h1 className='all-text-title'>Olympiads</h1>
                    <p className='description'>
                        During high school, I participated in many stadual and national olympiads.
                        I got more than 30 medals and developed my logical thinking in many areas. 
                        Besides, I created relationships that I carry until today. This networking
                        made me grow.
                    </p>
                </div>
            </div>
            <div className='all-text'>
                <div className='text'>
                    <h4 className='icon'>University Decision <br/><span>2020</span></h4>
                    <h1 className='all-text-title'>Prep and ITA</h1>
                    <p className='description'>
                        In the last year of High School, I participated in a program called Prep, from Fundação Estudar,
                        which prepare students to apply for american universities. The application process was of great 
                        personal growth and discovery. At the same time, I studied for ITA's entrance exam, one of the hardest
                        in Brazil, in which less than 200 students are accepted each year. In the end, I was waitlisted in Columbia 
                        and accepted at ITA.  
                    </p>
                </div>
            </div>
            <div className='all-text'>
                <div className='text'>
                    <h4 className='icon'> ITA <br/> First Semester<br/> <span>2021</span></h4>
                    <h1 className='all-text-title'>Activities opportunities</h1>
                    <p className='description'>
                        I embraced my opportunities as a Freshman at ITA and entered at the clubs that caught my eye.
                        First, I entered at ITA Jr., a web deve company run by ITA's students. I studied web development and
                        learned project management. 
                        Second,  I got into CEE, the internship and employement comission of ITA, in which I got in contact
                        with most brazilian companies and understood selective processes.
                        Third, I entered ITA Bits, in which I learned competitive programming.
                    </p>
                </div>
                <div className='text'>
                    <h1 className='all-text-title'>Professional opportunities</h1>
                    <p className='description'>
                        Fundação Estudar helped me to get professional opportunies already in the first semester.
                        I participated in their summer internship from march to june in which I automated processes.
                        After that, I took part in Harmony Analytics data science and preparo vc 
                        web development summers intership.
                    </p>
                </div>
            </div>
        </div>
    )
}

export default About