import './Card.css' 
import {IoLogoGithub} from "react-icons/io5";

function Card({imgLink, title, text, date, gitLink}) {

    return (
        <div className='card'>
            <div className='texts'>
                <p className='card-title'>
                    {title}
                </p>
                <p className='card-text'>
                    {text}
                </p>
                <div className='card-footer'>
                    <p className='card-date'>
                        {date}
                    </p>
                    <a href={gitLink} className='card-link'>
                        <IoLogoGithub  color = {'black' } size={20} />
                    </a>
                </div>
            </div>
        </div>
    )
}

export default Card