import './Home.css' 
import particlesConfig from './particles_config.json'
import Particles from "react-tsparticles";

function Home() {

    return (
        <div id='home'>
            <Particles className='home-particles' options={particlesConfig} id="tsparticles"/>
            <div className='home-container' >
                <h1 className='intro'>Hello!  I'm  <span>Felipe Spirandelli </span> </h1>
                <h2 className='secondary'>An Aspiring <span>Software Engineer</span></h2>
            </div>
        </div>
    )
}

export default Home