import Card from '../Card/Card'
import ScrollBox from '../Scrollbox/Scrollbox'
import './Project.css'
import PROJECTS from './Projects.json'

function Project() {



    return (
        <div id='projects'>
            <h1 className='title'>Projects</h1>
            <ScrollBox >
                {PROJECTS.map(({imgLink, title, text, date, gitLink, key}) =>(
                    <Card 
                    key={key}
                    imgLink={imgLink}
                    title={title}
                    text={text}
                    date={date}
                    gitLink={gitLink} />
                ))}
            </ScrollBox>
        </div>
    )
}

export default Project