import './Footer.css'
import { HiOutlineMail } from "react-icons/hi";
import { FaLinkedinIn } from "react-icons/fa";
import { IoLogoGithub } from "react-icons/io5";

function Footer() {

    let width = window.innerWidth

    return (
        <div id='footer'>
            <h3 className='title footer-title'> Contact me</h3>
            <div className='footer-contacts'>
                <div className='footer-logo'>
                    <h1 className='logo'>FelipeSpirandelli</h1>
                </div>
                <div className='footer-icons'>
                    <div className='footer-icon-text'>
                        <a className='footer-links' href='mailto: felipereisspirandelli@gmail.com'>
                            <HiOutlineMail color={'white'} size={20} />
                            {width > 800 ?
                                <p className='contact-info'>
                                    felipereisspirandelli@gmail.com
                                </p>
                                :
                                null
                            }
                        </a>
                    </div>
                    <div className='footer-icon-text'>
                        <a className='footer-links' href='https://www.linkedin.com/in/felipereisspirandelli/'>
                            <FaLinkedinIn color={'white'} size={20} />
                            {width > 800 ?
                                <p className='contact-info'>
                                    /felipespirandelli
                                </p>
                                :
                                null
                            }
                        </a>
                    </div>
                    <div className='footer-icon-text'>
                        <a className='footer-links' href='https://github.com/FelipeSpirandelli'>
                            <IoLogoGithub color={'white'} size={20} />
                            {width > 800 ?
                                <p className='contact-info'>
                                    /FelipeSpirandelli
                                </p>
                                :
                                null
                            }
                        </a>
                    </div>
                </div>
            </div>
            <p className='footer-end'>Made by Felipe Spirandelli 2021</p>
        </div>
    )
}

export default Footer